@import url("https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap");
:root {
  --bb-nav: #1E1E1E;
  --bb-pink: #FF5C90;
  --bb-pink-light: #EA45A0;
  --bb-green: #32806F;
  --bb-green-light: #00AD45;
  --bb-fluo: #64FF4F;
  --bb-blue: #3B86E9;
  --bb-dark-blue: #3F4C5E;
  --bb-salmon: #FF8A8A;
  --bb-purple: #8955FC;
  --bb-purple-light: #8850FE;
  --bb-grey: #202225;
  --bb-red: #ED2D45;
  --bb-white: #FFFFFF;
  --bb-black: #000000;
}

@font-face {
  font-family: "Dogica";
  src: url(../../fonts/OTF/dogica.otf);
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "VHSGothic";
  src: url(../../fonts/VHSGhotic/vhs-gothic.ttf);
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Visitor";
  src: url(../../fonts/Visitor/visitor1.ttf);
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Retro-Computer";
  src: url(../../fonts/TTF/retro_computer.ttf);
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
.share-tech-mono-regular {
  font-family: "Share Tech Mono", monospace;
  font-weight: 400;
  font-style: normal;
}

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: "Retro-Computer", sans-serif;
}

body {
  position: relative;
  background: var(--bb-nav);
}

nav {
  position: fixed;
  width: 100%;
  height: 100px;
  z-index: 10;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
}

li {
  list-style: none;
}

.wrapper {
  margin: 0 auto;
  width: 100%;
  max-width: 1920px;
  height: 100%;
}

.nav-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.nav-menu a,
.nav-menu button,
.nav-menu li {
  cursor: pointer;
}
.nav-menu__mobile {
  display: none;
}
.nav-menu__logo, .nav-menu__others {
  width: 20%;
}
.nav-menu__links {
  padding: 0.5rem 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 60%;
  height: 100%;
  text-transform: uppercase;
  font-size: 1.4rem;
  color: var(--bb-white);
}
.nav-menu__link {
  padding: 1rem 2rem;
  background: var(--bb-grey);
  border-radius: 0.5rem;
  transition: background 0.4s;
}
.nav-menu__link:hover {
  background: var(--bb-green);
}
.nav-menu__others {
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav-menu__others-link {
  padding: 2rem 2.6rem;
  background: var(--bb-purple);
  color: var(--bb-white);
  border-radius: 0.5rem;
  font-size: 1.4rem;
  font-weight: 600;
  transition: background 0.4s;
}
.nav-menu__others-link:hover {
  background: var(--bb-white);
  color: var(--bb-nav);
}

header video {
  max-height: 100vh;
  object-fit: cover;
  overflow-clip-margin: content-box;
  overflow: clip;
}

.tickets {
  background: url(../img/ticketsBg.png);
  background-size: cover;
  background-position: top;
  height: 100vh;
  font-family: "Retro-Computer", sans-serif;
}
.tickets__container {
  padding: 3rem 0 0;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 4rem;
  height: 100%;
}
.tickets__container h2 {
  margin: 8rem 0 0;
  font-size: 3.2rem;
  text-transform: uppercase;
  color: var(--bb-white);
}
.tickets__box {
  padding: 0 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  max-width: 1020px;
  width: 100%;
  height: 70%;
}
.tickets__ticket {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.tickets__ticket img {
  width: 100%;
  height: auto;
  max-width: 285px;
  border-radius: 1.3rem;
  box-shadow: 0px 0px 15px 2px #696969;
}
.tickets__ticket button {
  margin: 7rem auto 0;
  padding: 1.2rem 2.4rem;
  font-size: 1.5rem;
  font-family: "Retro-Computer", sans-serif;
  border-radius: 1.4rem;
  border: none;
  color: var(--bb-white);
  box-shadow: 0 0 10px 0px #696969;
  transition: background 0.4s, color 0.4s;
  cursor: pointer;
}
.tickets__ticket:nth-of-type(1) button {
  background: var(--bb-purple-light);
}
.tickets__ticket:nth-of-type(2) button {
  background: var(--bb-green-light);
}
.tickets__ticket:nth-of-type(3) button {
  background: var(--bb-pink-light);
}
.tickets__ticket button:hover {
  background: var(--bb-white);
  color: var(--bb-black);
}

.line-up {
  position: relative;
  height: 100vh;
  background: url(../img/lineUpBg.png);
  background-size: cover;
  background-position: center;
}
.line-up__box {
  margin: 0 auto;
  padding: 10rem 5rem;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1350px;
  height: 100%;
  border-radius: 1.6rem;
}
.line-up__localization-box {
  padding: 2.4rem 3.6rem;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 20%;
}
.line-up__localization-box h2 {
  padding: 2.2rem 5rem;
  font-size: 1.8rem;
  text-transform: uppercase;
  color: var(--bb-white);
  border: 5px solid #cdacc1;
  border-radius: 1.4rem;
}
.line-up__header {
  display: flex;
}
.line-up__title {
  padding: 2rem 0;
  color: var(--bb-pink-light);
  font-size: 1.8rem;
  text-transform: uppercase;
  width: 100%;
}
.line-up__btns {
  display: flex;
  gap: 2rem;
}
.line-up__btns button {
  padding: 2rem 2.4rem;
  color: var(--bb-white);
  border: 1px solid var(--bb-white);
  border-radius: 1rem;
  font-size: 1.4rem;
  font-weight: 900;
  text-transform: uppercase;
  border: none;
  transition: background 0.4s, color 0.4s;
  cursor: pointer;
}
.line-up__btns button:nth-of-type(1) {
  background: var(--bb-purple);
}
.line-up__btns button:nth-of-type(1):hover {
  background: var(--bb-white);
  color: var(--bb-purple);
}
.line-up__btns button:nth-of-type(2) {
  background: var(--bb-red);
}
.line-up__btns button:nth-of-type(2):hover {
  background: var(--bb-white);
  color: var(--bb-red);
}
.line-up__artist-box {
  padding: 2rem 0;
  display: flex;
  align-items: center;
  align-self: center;
  height: 50%;
}
.line-up__artist-box span {
  color: var(--bb-white);
  font-size: 1.8rem;
  font-weight: 700;
  text-transform: uppercase;
}
.line-up__footer {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  height: 30%;
}
.line-up__bbtv {
  max-width: 210px;
  font-size: 2.4rem;
  text-align: center;
  color: var(--bb-white);
}
.line-up__yt {
  max-width: 220px;
}
.line-up__yt img {
  width: 100%;
  height: auto;
}

.info {
  position: relative;
  background: url(../img/infoBg.png);
  background-size: cover;
  aspect-ratio: 16/9;
}
.info__container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.info__box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  max-width: 992px;
  height: 50%;
}
.info__title h2 {
  font-size: 3.4rem;
  color: var(--bb-white);
}
.info__faq {
  margin: 0 0 10rem;
  padding: 0 4rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
  width: 100%;
}
.info__faq button {
  padding: 2rem 0;
  border-radius: 1.2rem;
  border: none;
  font-size: 1.8rem;
  font-family: "Retro-Computer", sans-serif;
  color: var(--bb-white);
}
.info__faq button:nth-of-type(1) {
  background: var(--bb-pink-light);
}
.info__faq button:nth-of-type(2) {
  background: var(--bb-green);
}
.info__faq button:nth-of-type(3) {
  background: var(--bb-blue);
}
.info__faq button:nth-of-type(4) {
  background: var(--bb-purple);
}

.email {
  position: relative;
  height: 100vh;
  background: url(../img/emailBg.png);
  background-size: cover;
  background-position: center;
  z-index: 0;
}
.email::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(../img/emailHeroBg.png);
  background-size: cover;
  background-position: center;
  opacity: 0.6;
  z-index: -1;
  content: "";
}
.email__container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.email__box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 640px;
  height: 100%;
}
.email__logo img {
  width: 100%;
  height: auto;
  max-width: 150px;
}
.email__title {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 15%;
  width: 100%;
}
.email__title h2 {
  margin: 0 4rem 0 0;
  display: block;
  font-size: 4.2rem;
  color: var(--bb-white);
  word-spacing: 150px;
}
.email__form {
  padding: 5rem 3rem 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 2rem;
  height: 59%;
}
.email__form h3 {
  font-size: 2rem;
  color: var(--bb-white);
}
.email__form form {
  display: grid;
  grid-template-rows: repeat(3, 1fr);
}
.email__form form input[type=text] {
  margin: 1rem 0;
  padding: 2rem;
  height: 60px;
  border-radius: 1.2rem;
  line-height: 0.4rem;
  border: none;
}
.email__form form input[type=text]::placeholder {
  font-family: "VHSGothic", sans-serif;
  font-size: 1.7rem;
  text-transform: uppercase;
  color: var(--bb-pink);
}
.email__form button {
  padding: 2rem 5rem;
  color: var(--bb-white);
  background: var(--bb-purple);
  border-radius: 1.2rem;
  border: none;
  font-size: 1.8rem;
  text-transform: uppercase;
  font-family: "VHSGothic", sans-serif;
}
.email__agree-box {
  position: relative;
  padding: 2rem 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;
}
.email__agree-box label {
  font-size: 0.9rem;
  color: var(--bb-white);
}
.email__agree-box input[type=checkbox] {
  position: absolute;
  opacity: 0;
  height: 25px;
  width: 22px;
}
.email__agree-box span {
  margin-right: 1rem;
  display: inline-block;
  background-color: white;
  box-sizing: border-box;
  width: 40px;
  height: 25px;
  border-radius: 0.2rem;
}
.email__agree-box span:has(> input:checked) {
  background-color: var(--bb-purple);
}

@media screen and (max-width: 992px) {
  .nav-menu {
    justify-content: flex-end;
  }
  .nav-menu__mobile {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .nav-menu__mobile button {
    color: var(--bb-white);
    background-color: transparent;
    border: none;
    width: 40px;
    cursor: pointer;
  }
  .nav-menu__logo, .nav-menu__mobile, .nav-menu__others {
    width: 30%;
  }
  .nav-menu__links {
    position: absolute;
    top: 0;
    left: 0;
    display: none;
    flex-direction: column;
    justify-content: space-evenly;
    width: 100vw;
    height: 100vh;
    background: white;
  }
}
@media screen and (max-width: 769px) {
  .media__box-top, .media__box-bottom {
    flex-direction: column;
    aspect-ratio: 2/3;
    height: 100%;
  }
  .media__box-left, .media__box-right, .media__box-social-left {
    padding: 2rem;
    width: 100%;
    height: 100%;
  }
}
@media screen and (max-width: 600px) {
  .localization__box h2 {
    padding: 1rem 2.2rem;
    font-size: 1.2rem;
    text-align: center;
    line-height: 1.8;
  }
  .line-up__box {
    padding: 2rem;
  }
  .line-up__btns button {
    padding: 1.2rem 2rem;
  }
  .line-up__artist-box span {
    font-size: 1.2rem;
    text-align: center;
    line-height: 2;
  }
}
@media screen and (max-width: 565px) {
  nav {
    height: 60px;
  }
  .nav-menu__mobile button {
    width: 30px;
  }
  .nav-menu__others-link {
    padding: 1rem 1.2rem;
    font-size: 1.1rem;
  }
  .tickets {
    padding: 2rem 0 4rem;
    height: auto;
  }
  .tickets__container h2 {
    font-size: 2.3rem;
  }
  .tickets__box {
    flex-direction: column;
    gap: 4rem;
  }
  .tickets__ticket img {
    max-width: 280px;
  }
}